import MyPagination from "../../../components/MyPagination";
import mixins from "../../../utils/mixins";
import {common} from "../../../utils/api/url";
import {areaTypeText, MessageError, MessageSuccess, MessageWarning, setChildren} from "../../../utils/utils";
import LayoutTb from "../../../components/Layout/LayoutTb";
import MySelect from "../../../components/MySelect";
import MyUserType from "../../../components/utils/MyUserType";
import MyDatePicker from "../../../components/MyDatePicker";
import MyInputButton from "../../../components/search/MyInputButton";
import DialogNormal from "../../../components/utils/dialog/DialogNormal";
import MyNormalDialog from "../../../components/utils/dialog/MyNormalDialog";
import MyElTree from "../../../components/tree/MyElTree";
import homeworkObjectValidater from "../../../utils/validater/homeworkObjectValidater";
export default {
  mixins: [mixins, homeworkObjectValidater],
  components: {MyPagination,LayoutTb,MySelect,MyUserType,MyDatePicker,MyInputButton,DialogNormal,MyNormalDialog,MyElTree},
  data(){
    return {
      tableData: [],
      searchDate: [],
      typeList: [],
      searchKey: '',
      visible: false,
      modalVisible: false,
      dialogLoading: false,
      visibleConfim: false,
      mutiDeleteLoading: false,
      editMutiStatus: false,
      clearTime: '',
      action: '',
      subTitle: '',
      weightStatus: [],
      defaultCheckedKeys: [],
      deviceList: [],
      searchCollege: '',
      searchMajor: '',
      searchGrade: '',
      searchClass: '',
      form: {
        id: '',
        type: '',
        name: '',
        weight: '',
        area: '',
        areaData: []
      }
    }
  },
  created() {
    this.init();
    this.initWeight();
  },
  methods: {
    init(){
      let params = {
        page: this.page,
        num: this.num,
        checkType: 2,
        orderAttr: 'check_item',
        orderAsc: false
      };
      params['collegeId'] = this.searchCollege;
      params['majorId'] = this.searchMajor;
      params['grade'] = this.searchGrade;
      params['classId'] = this.searchClass;
      this.$axios.get(common.housework_query_check_item_page, {params: params}).then(res => {
        if (res.data.data){
          this.tableData = res.data.data.list;
          this.total = res.data.data.totalCount;
          this.num = res.data.data.num;
          this.page = res.data.data.currentPage;
        }
      });
    },
    getRowKeys(row) {
      return row.id
    },
    initWeight(){
      this.weightStatus = [];
      for (let i = 0; i < 9; i++){
        this.weightStatus.push({
          label: i+1,
          value: i+1
        });
      }
    },
    addInfo(){
      this.modalVisible = true;
    },
    editMutiInfo(){
      this.editMutiStatus = true;
      this.modalVisible = true;
    },
    deleteMutiInfo(){
      let arr = [];
      for (let i = 0; i < this.deviceList.length; i++){
        arr.push(this.deviceList[i].id);
      }
      let params = {
        idSet: arr.join(),
        reset: true
      };
      params = this.$qs.stringify(params);
      this.mutiDeleteLoading = true;
      this.$axios.post(common.delete_batch_object, params).then(res => {
        if (res.data.code == 200){
          this.init();
          this.deviceList = [];
          this.$refs.refTable.clearSelection();
          MessageSuccess(res.data.desc);
        }else {
          MessageError(res.data.desc);
        }
        this.mutiDeleteLoading = false;
      });
    },
    search(data){
      this.searchKey = data.input;
      this.page = 1;
      this.init();
    },
    sizeChange(event){
      this.page = 1;
      this.num = event;
      this.init();
    },
    currentPage(event){
      this.page = event;
      this.init();
    },
    jumpPage(data){
      this.page = data;
      this.init();
    },
    nodeClick(data){
      this.searchCollege = "";
      this.searchMajor = "";
      this.searchGrade = "";
      this.searchClass = "";
      this.searchBuild = "";
      this.searchFloor = "";

      if (data.unit == 1){
        this.searchCollege = data.id;
      }else if (data.unit == 2){
        this.searchCollege = data.college_id;
        this.searchMajor = data.id;
      }else if (data.unit == 3){
        this.searchMajor = data.major_id;
        this.searchGrade = data.grade;
      }else if (data.unit == 4){
        this.searchClass = data.id;
      }

      this.page = 1;
      this.init();
    },
    handleSelect(data, type){
      switch (type) {
        case 1:
          this.form.area = data;
          break
        case 2:
          this.form.weight = data;
          break
      }
    },
    areaTypeInfo(val){
      return areaTypeText(val);
    },
    editInfo(row){
      this.form = {
        id: row.id,
        type: '',
        name: row.check_item,
        weight: row.weight,
        area: ''+row.scope_type
      };
      this.modalVisible = true;
    },
    deleteInfo(row){
      this.form.id = row.id;
      this.subTitle = row.check_item;
      this.visibleConfim = true;
    },
    handleOkChange(data) {
      this.dialogLoading = true;
      let url = "";
      let params = {
        id: this.form.id
      };
      url = common.housework_query_check_item_del;
      params = this.$qs.stringify(params);
      this.$axios.post(url, params).then(res => {
        if (res.data.code == 200){
          this.init();
          MessageSuccess(res.data.desc);
        }else {
          MessageError(res.data.desc);
        }
        this.visibleConfim = false;
        this.dialogLoading = false;
      });
    },
    handleCancelChange(data) {
      this.visibleConfim = false;
    },
    cancelDialog(){
      this.modalVisible = false;
    },
    closeDialog(event){
      let _self = this;
      this.form = {
        id: '',
        type: '',
        name: '',
        weight: '',
        area: '',
        areaData: []
      };
      this.subTitle = "";
      this.editMutiStatus = false;
      if (_self.$refs['areeTree']){
        var node = this.$refs.areeTree.$refs.tree.getCheckedNodes();
        node.forEach((item) => {
          this.$nextTick(() => {
            this.$refs.areeTree.$refs.tree.setChecked(item.id, false,true);
          });
        });
      }
      if (this.$refs['form']){
        this.$refs['form'].resetFields();
      }
    },
    okDialog(event){
      let url = "";
      let objArr = [];
      this.$refs['form'].validate((valid) => {
        if (valid) {
          if (this.form.id == '' && this.editMutiStatus == false){
            objArr  = this.setAreaChildren(this.$refs.areeTree.$refs.tree.getCheckedNodes());
            if (objArr.length == 0){
              MessageWarning(this.$t("请选择范围"));
              return;
            }
          }

          this.dialogLoading = true;
          let params = {
            checkType: 2,
            checkItem: this.form.area == 3 ? this.$t("空床") : this.form.name,
            scopeType: this.form.area,
            weight: this.form.weight
          };

          if (this.form.id != ''){
            url = common.housework_query_check_item_edit;
            params['id'] = this.form.id;
          }else if (this.editMutiStatus == true){
            url = common.edit_batch_object;
            let arr = [];
            for (let i = 0; i < this.deviceList.length; i++){
              arr.push(this.deviceList[i].id);
            }
            params['idSet'] = arr.join();
          }else {
            url = common.housework_query_check_item_save;
            params['classIdSet'] = objArr.join();
          }

          params = this.$qs.stringify(params);
          this.$axios.post(url, params, {loading: false}).then(res => {
            if (res.data.code == 200){
              this.modalVisible = false;
              this.init();
              this.deviceList = [];
              this.$refs.refTable.clearSelection();
              MessageSuccess(res.data.desc);
            }else {
              MessageError(res.data.desc);
            }
            this.dialogLoading = false;
          });
        }
      });
    },
    setAreaChildren(tree){//迭代方法
      let _self = this;
      let obj = [];
      if (tree && tree.length > 0){
        tree.map(function (item,index) {
          if (item.unit == 4){
            obj.push(item.id);
          }

          if(item['children'] != undefined && item['children'].length > 0){
            _self.setAreaChildren(item['children']);
          }
        });
      }
      return obj;
    },
    handleSelectionChange(data){
      this.deviceList = data;
    }
  }
}
